<template>
  <div>
    <template
      v-for="route of routes"
    >
      <div
        v-if="route.children && route.children.length"
        :key="route.path"
      >
        <el-submenu
          v-if="submenuShow(route)"
          :index="route.path"
        >
          <template slot="title">
            <svg-icon
              :class-name="route.meta.icon || 'default-menu'"
            />
            <span
              slot="title"
            >
              {{ $t(`${route.meta.title}`) }}
            </span>
          </template>
          <submenu-component
            :routes="route.children"
            class="menu__nest"
          />
        </el-submenu>
        <submenu-component
          v-else
          :routes="route.children"
        />
      </div>
      <template v-else-if="submenuShow(route)">
        <router-link
          :key="route.path"
          class="menu-item_link"
          :to="{name: route.name}"
        >
          <el-menu-item
            :index="route.path"
          >
            <svg-icon
              :class-name="route.meta.icon || 'default-menu'"
            />
            <span slot="title">{{ $t(`${route.meta.title}`) }}</span>
          </el-menu-item>
        </router-link>
      </template>
    </template>
  </div>
</template>

<script>
export default {
  name: 'SubmenuComponent',
  props: {
    routes: {
      type: Array,
      default() {
        return []
      },
    },
  },
  methods: {
    submenuShow(route) {
      return route.meta && (route.meta.sidebarShow === undefined || route.meta.sidebarShow) && route.meta.title
    },
  },
}
</script>

<style lang="scss" scoped>
.svg-icon {
  width: 14px;
  height: 14px;
  vertical-align: -0.25em;
}
</style>
